<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
         <div class="sm-form ">
             <label for="floorplan">Floorplan</label>
             <input type="file" id="filenya" name="floorplan" class="form-control form-control-sm" placeholder="floorplan" @change="upload">
             <input type="text" id="floorplan" name="floorplan" class="form-control form-control-sm" placeholder="floorplan" v-model="vdata.floorplan" >
         </div>
         <div class="row">
             <div class="col-6">
                <div class="sm-form ">
                    <label for="nilai1">Your nilai1</label>
                    <input type="text" id="nilai1" name="nilai1" class="form-control form-control-sm" placeholder="nilai1" v-model="vdata.nilai1" >
                </div>
                <div class="sm-form ">
                    <label for="nilai2">Your nilai2</label>
                    <input type="text" id="nilai2" name="nilai2" class="form-control form-control-sm" placeholder="nilai2" v-model="vdata.nilai2" >
                </div>
                <div class="sm-form ">
                    <label for="nilai3">Your nilai3</label>
                    <input type="text" id="nilai3" name="nilai3" class="form-control form-control-sm" placeholder="nilai3" v-model="vdata.nilai3" >
                </div>

             </div>
             <div class="col-6">
                <div class="sm-form ">
                    <label for="nilai4">Your nilai4</label>
                    <input type="text" id="nilai4" name="nilai4" class="form-control form-control-sm" placeholder="nilai4" v-model="vdata.nilai4" >
                </div>
                <hr class="mt-2 mb-2">
                <div class="sm-form ">
                    <label for="center1">Your center1</label>
                    <input type="text" id="center1" name="center1" class="form-control form-control-sm" placeholder="center1" v-model="vdata.center1" >
                </div>
                <div class="sm-form ">
                    <label for="center2">Your center2</label>
                    <input type="text" id="center2" name="center2" class="form-control form-control-sm" placeholder="center2" v-model="vdata.center2" >
                </div>

             </div>
         </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <!-- <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {
                        nilai1:'0.0004',
                        nilai2:'0.0011',
                        nilai3:'0.0095',
                        nilai4:'0.0007',
                        center1:'-2.3615836073925434',
                        center2:'115.46956926581515'
                    };
                "
                v-if="datanya.length>0"
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
                v-if="datanya.length>0"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div> -->
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
export default {
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {
          nilai1:'0.0004',
          nilai2:'0.0011',
          nilai3:'0.0095',
          nilai4:'0.0007',
          center1:'-2.3615836073925434',
          center2:'115.46956926581515'
      },
      btn: "tambah",
      datanya:[],
    };
  },
  layout:'app_admin2',
  methods: {
      upload(){
          let file = document.querySelector("#filenya"); // berikan id pada input file
          sdb.collection('tbuser',false,urlnya).upload(file,false,true) // file,compress,scramble
          .then(res=>{
              console.log(res)
              this.vdata.floorplan=res;
              this.$forceUpdate();
          })
      },
    getData(){
      sdb.collection("app_gis_marker",false,urlnya).doc().select(`select floorplan,link3d,nilai1,nilai2,nilai3,nilai4,center1,center2 from app_gis_marker where id_peta='${this.$route.query.id}' AND kategori='floorplan'`).then(res => {
        this.datanya=res;
        if(this.datanya.length>0){
            this.btn='update'
            this.vdata=res[0]
        }else{
            this.btn='tambah'
        }
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_gis_marker",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_gis_marker",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_gis_marker",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
    this.getData();
  },
};
</script>